.loading-spinner {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;

    .spinner {
        border: 4px solid #EDEFEE;
        border-top: 4px solid #495E57;
        border-radius: 50%;
        width: 30px;
        height: 30px;
        animation: spin 1s linear infinite;
    }
}

@keyframes spin {
    0% {
        transform: rotate(0deg);
    }

    100% {
        transform: rotate(360deg);
    }
}